<template>
  <div class="app-box">
    <div class="header-box">
      <img src="../assets/return.png" alt="" v-if="!firstopen" class="return-btn" @click="returnBtn">
      数字资产暴增器
    </div>
    <div class="first-box" v-if="firstopen">
      <div id="canvas_box" class="canvas_image_box">
        <img src="../assets/img@2x.png" class="bg-img" alt="">
        <div class="coinname" :class="{'toimg':toimg}">
          <img src="../assets/coinnamebg@3x.png" class="coin-name-bg" alt="">
          <span>{{coinname}}</span>
          <span class="coin-name">{{coinname}}</span>
        </div>
        <div class="money">{{moneynum}}</div>
        <div class="money-box">
          <img :src="coinimg" class="logo-img" alt="">
          <div class="lt coin-name-font">{{coinname}}</div>
        </div>
        <div class="money-gt-box">
          <div class="money-first">{{coinnum}}</div>
          <div class="money-second">≈ ￥{{moneynum}}</div>
        </div>
      </div>
      <div class="select-center-box">
        <el-select class="select-box"  v-model="coinname" placeholder="请选择">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <el-input class="select-box" @input.native="numberChange" type="number" v-model="coinnum" placeholder="输入币种数量"></el-input>
      </div>
      <div class="fool-btn" @click="getImage">
        一键生成
      </div>
    </div>
    <div class="sec-box" v-else>
      <img :src="finishImg" class="finish-img" alt="">
      <div id="save-tip" v-if="tipShow">
        <div class="finger"></div>
        <span>长按图片</span>
        <span>即可保存到相册</span>
      </div>
      <div class="fool-btn" @click="downloadImg">
        保存图片到相册
      </div>
    </div>
    <div class="footer-rol">比特派出品 <a href="www.bitpie.com" target="_blank">bitpie.com</a></div>
  </div>


</template>

<script>
  import html2canvas from 'html2canvas';
  export default {
    name: "img",
    data(){
      return {
        options:[
          {
            value:'BTC',
            label:'BTC'
          },
          {
            value:'ETH',
            label:'ETH'
          },
          {
            value:'EOS',
            label:'EOS'
          },
          {
            value:'USDT',
            label:'USDT'
          },
          {
            value:'BCH',
            label:'BCH'
          },
          {
            value:'BSV',
            label:'BSV'
          },
          {
            value:'TRX',
            label:'TRX'
          },
          {
            value:'DASH',
            label:'DASH'
          },
          {
            value:'ETC',
            label:'ETC'
          },
          {
            value:'ZEC',
            label:'ZEC'
          },
          {
            value:'DOGE',
            label:'DOGE'
          },
          {
            value:'QTUM',
            label:'QTUM'
          },
          {
            value:'HSR',
            label:'HSR'
          },
          {
            value:'HC',
            label:'HC'
          },
          {
            value:'BOS',
            label:'BOS'
          },
          {
            value:'BTM',
            label:'BTM'
          }
        ],
        coinname:'BTC',
        coinimg:'../../static/img/btc@2x.png',
        imgarr:{
          BTC:{
            img:'../../static/img/btc@2x.png',
            hl:26953.16
          },
          ETH:{
            img:'../../static/img/eth@3x.png',
            hl:940.23
          },
          EOS:{
            img:'../../static/img/eos@2x.png',
            hl:17.12
          },
          USDT:{
            img:'../../static/img/usdt@2x.png',
            hl:6.79
          },
          LTC:{
            img:'../../static/img/ltc@2x.png',
            hl:4710.83
          },
          BCH:{
            img:'../../static/img/bch@2x.png',
            hl:1133.8
          },
          BSV:{
            img:'../../static/img/svg@2x.png',
            hl:439.3
          },
          TRX:{
            img:'../../static/img/trx@2x.png',
            hl:0.15
          },
          DASH:{
            img:'../../static/img/dash@2x.png',
            hl:618.25
          },
          ETC:{
            img:'../../static/img/etc@2x.png',
            hl:32.22
          },
          ZEC:{
            img:'../../static/img/zec@2x.png',
            hl:373.82
          },
          DOGE:{
            img:'../../static/img/icon_currency_doge@2x.png',
            hl:0.01
          },
          QTUM:{
            img:'../../static/img/qtum.png',
            hl:17.25
          },
          HSR:{
            img:'../../static/img/hsr@2x.png',
            hl:4.42
          },
          HC:{
            img:'../../static/img/hsr@2x.png',
            hl:9.24
          },
          BOS:{
            img:'../../static/img/bos_big@2x.png',
            hl:9.24
          },
          BTM:{
            img:'../../static/img/btm@2x.png',
            hl:0.12
          }

        },
        coinnum:null,
        moneynum:26953000,
        toimg:true,
        firstopen:true,
        finishImg:null,
        tipShow:false
      }
    },
    watch:{
      coinname (val) {
        this.moneynum = this.imgarr[val].hl * this.coinnum
        this.coinimg = this.imgarr[val].img
      },
      coinnum(val){
        this.moneynum = val*this.imgarr[this.coinname].hl
      }
    },
    methods:{
      returnBtn(){
        this.finishImg = null
        this.firstopen = true
      },
      downloadImg(){
        this.tipShow = true
        setTimeout(function () {
          this.tipShow = false
        },2000)
      },
      numberChange(){
        console.log(this.coinnum)
        if(this.coinnum>100000000){
          this.$message({
            message: '再填就上亿啦',
            type: 'warning'
          });
          this.$nextTick(() => {
            this.coinnum = 99999999
          })

        }
        if(this.coinnum.toString().indexOf(".") !=-1&&this.coinnum.toString().split(".")[1].length>4){

          this.$nextTick(() => {
            this.coinnum = this.coinnum.toFixed(4)
          })
        }
      },
      getImage () {
        if(!this.coinnum){
          this.$message({
            message: '请输入币种数量',
            type: 'warning'
          });
          return false
        }
        this.toimg = false
        var shareContent = document.getElementById('canvas_box');
        var width = shareContent.offsetWidth;
        var height = shareContent.offsetHeight;
        var canvas = document.createElement("canvas");
        var scale = 4;
        canvas.width = width * scale;
        canvas.height = height * scale;
        canvas.getContext("2d").scale(scale, scale);
        var that  = this
        var opts = {
          scale: scale,
          canvas: canvas,
          logging: true,
          width: width,
          height: height
        };
        html2canvas(shareContent,opts).then(function(canvas) {

          that.finishImg = canvas.toDataURL()
          that.firstopen = false
          that.tipShow = true
          setTimeout(function () {
            that.tipShow = false
          },2000)
        });
      }
    }
  }
</script>

<style scoped>
  .app-box{
    width:100%;
  }
  html,body{
    width:100%;
    margin:0;
    padding:0;
  }
  .canvas_image_box{
    width:375px;
    height:670px;
    position:relative;
    margin:0 auto;
    margin-bottom:20px;
  }
  .bg-img{
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
  }
  .coinname{
    font-family: PingFangSC-Medium;
    color:#fff;
    font-size:12px;
    position:absolute;
    top:32px;
    left:20px;
    height:16px;
    line-height:16px;
    padding-left:7px;
    padding-right:20px;
  }
  .coin-name{
    position:absolute;
    top:0;
    left:7px;
    color:#fff;
  }
  .coinname span{
    color:#fff;
  }
  .money{
    font-family: Avenir-Medium;
    font-size: 26px;
    color: #FFFFFF;
    letter-spacing: 0;
    text-align: left;
    position:absolute;
    top:117px;
    left:33px;
  }
  .money-box{
    left:22px;
    top:416px;
    position:absolute;
  }
  .logo-img{
    width:22px;
    height:22px;
    float:left;
    margin-right:10px;
  }
  .coin-name-font{
    font-family: PingFangSC-Medium;
    font-size: 13px;
    color: #333333;
    letter-spacing: 0;
    line-height:22px;
    float:left;
  }
  .money-gt-box{
    width:150px;
    position:absolute;
    right:30px;
    top:409px;
    text-align: right;
  }
  .money-first{
    font-family: PingFangSC-Regular;
    font-size: 15px;
    color: #333333;
    text-align: right;
    line-height:1.2em;
  }
  .money-second{
    ont-family: PingFangSC-Regular;
    font-size: 11px;
    color: #999999;
    letter-spacing: 0;
    text-align: right;
    line-height:1.2em;
  }
  .fool-btn{
    height:80px;
    width:70%;
    margin:0 auto;
    line-height:80px;
    text-align: center;
    color:#fff;
    font-size:28px;
    background:#ff4303;
    margin-bottom:15px;
    cursor:pointer;
    font-weight:500;
    border-radius:5px;
  }
  .alert-box{
    background:rgba(0,0,0,0.4);
    width:100%;
    height:100%;
    position:fixed;
    top:0;
    left:0;
  }
  .choose-box{
    height:300px;
    background:#fff;
    position:fixed;
    left:0;
    bottom:0;
    width:100%;
    overflow-y:auto;
  }
  .select-box{
    width:70%;
    margin:0 auto;
    margin-bottom:30px;
    font-size:28px;

  }
  .select-center-box{
    text-align: center;
    margin-bottom:20px;
  }
  .header-box{
    height: 100px;
    line-height: 100px;
    background: #ff4303;
    color: #fff;
    font-size: 36px;
    text-align: center;
    margin-bottom:40px;
    position:relative;
  }
  .finish-img{
    width:100%;
    margin-bottom:60px;
  }
  .return-btn{
    position:absolute;
    top:30px;
    left:10px;
    height:40px;
    cursor:pointer;
  }
  #save-tip {
    position: absolute;
    background-color: rgba(0,0,0,.7);
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    border-radius: 10px;
    padding: 10px 15px;
    min-width: 154px;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%,-50%);
    transform: translate(-50%,-50%);
  }
  .finger {
    width: 50px;
    height: 50px;
    display: block;
    margin: 0 auto;
    background-image: url(../assets/finger.png);
    background-size: 50px 200px;
    background-position: 0 0;
    background-repeat: no-repeat;
  }
  .finger {
    -webkit-animation: finger 1s infinite steps(1) 0s forwards;
  }
  #save-tip span {
    display: block;
    color: #fff;
    text-align: center;
    font-size: 22px;
    line-height: 33px;
  }
  @-webkit-keyframes finger {
    0%,100% {
      background-position-y: 0
    }

    25% {
      background-position-y: -50px
    }

    50% {
      background-position-y: -100px
    }

    75% {
      background-position-y: -150px
    }
  }
  .coin-name-bg{
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
  }
  .footer-rol{
    text-align: center;
    font-size:24px;
    margin-top:40px;
  }
  .footer-rol a{
    color:#2d49c8;
    cursor: pointer;
  }
</style>
<style>
  .select-box .el-input__inner{
    height:80px !important;
    font-size:24px !important;
  }
  .el-select-dropdown__item{
    font-size:22px !important;
    height:50px !important;
    line-height:50px !important;
  }
</style>


